<template>
    <div>
        <mt-field label="项目编号" placeholder="签到项目代码" v-model="actionId"></mt-field>
        <mt-button type="primary" @click.native="checkIn">签到</mt-button>
        <mt-cell :title="title"></mt-cell>
    </div>
</template>
<script>
    import moment from "moment"
    export default {
        data() {
            return {
                actionId: '',
                title: '标题',
                label: '描述'
            }
        },
        methods: {
            checkIn() {
                let actionId = ''
                if (this.actionId) {
                    actionId = this.actionId
                    var url = '/actions/checkin';
                    this.$http.post(url, {
                            actionId: actionId
                        })
                        .then((response) => {
                            let body = response.body
                            alert(JSON.stringify(body))
                        });
                } else {
                    alert('action id cannot be empty!')
                }
            }
        }
    }

</script>



